@charset "UTF-8";
.m-small-img {
  box-sizing: border-box;
  width: 100%;
  height: 290px;
  padding: 10px;
  border-radius: 5px; }
  .m-small-img:hover {
    background-color: #f4f4f4; }
  .m-small-img .ellipsis {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }
  .m-small-img .img-wrapper {
    height: 120px;
    width: 100%;
    margin-bottom: 11px; }
    .m-small-img .img-wrapper img {
      height: 100%;
      width: 100%; }
  .m-small-img .title {
    margin-bottom: 6px;
    height: 22px;
    font-size: 16px;
    color: #222;
    line-height: 22px;
    font-weight: 500; }
  .m-small-img .subTitle {
    font-size: 12px;
    color: #999;
    margin-bottom: 10px; }
  .m-small-img .tag {
    font-size: 12px;
    color: #999;
    margin-bottom: 10px;
    height: 20px; }
    .m-small-img .tag span {
      margin-right: 10px;
      border: 1px solid #d8d8d8;
      padding: 0 3px; }
  .m-small-img .info {
    height: 27px; }
    .m-small-img .info .price {
      font-size: 22px;
      color: #F60;
      margin-right: 6px; }
      .m-small-img .info .price:before {
        font-size: 14px;
        content: "Â¥"; }
    .m-small-img .info .oldPrice {
      font-size: 12px;
      color: #999;
      text-decoration: line-through; }
    .m-small-img .info .bottomInfo {
      font-size: 12px;
      color: #999;
      float: right;
      margin-top: 10px; }
