@charset "UTF-8";
.m-big-img-home {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 10px;
  border-radius: 5px;
  font-size: 0;
  /*}*/ }
  .m-big-img-home:hover {
    /*background-color: #f4f4f4;*/ }
  .m-big-img-home .img-wrapper {
    position: relative;
    width: 100%;
    height: 208px;
    margin-bottom: 10px; }
    .m-big-img-home .img-wrapper .cover {
      width: 100%;
      height: 100%;
      border-radius: 5px; }
    .m-big-img-home .img-wrapper .header {
      position: absolute;
      top: 100%;
      right: 20px;
      margin-top: -27px;
      width: 52px;
      height: 52px;
      border: 2px solid #fff;
      border-radius: 50%; }
  .m-big-img-home .title, .m-big-img-home .des {
    width: 100%;
