  .m-big-img-home .foot-info {
    height: 27px; }
    .m-big-img-home .foot-info .price {
      font-size: 22px;
      color: #BE9E4D;
      margin-right: 6px; }
      .m-big-img-home .foot-info .price:before {
        font-size: 14px;
        content: "Â¥"; }
    .m-big-img-home .foot-info .source-price {
