







































.film-card {
    position: relative;
    height: 297px;

    .main-img {
        border-radius: 5px;
        width: 100%;
        height: 100%;
    }

    .film-mark {
        width: 78px;
        height: 20px;
        position: absolute;
        top: 10px;
        left: -6px;
        box-shadow: 0 2px 6px 0 rgba(0,0,0,.2);
    }

    .film-info {
        border-radius: 5px;
        position: absolute;
        box-sizing: border-box;
        padding-top: 48px;
        padding-left: 10px;
        width: 100%;
        bottom: 0;
        background-image: linear-gradient(-180deg,rgba(0,0,0,0) 0,rgba(29,45,55,.8) 99%);
        .score {
            font-weight: 500;
            font-size: 12px;
            color: #fff;
            >span {
                font-size: 16px;
                color: #FD9827;
            }
        }

        .title {
            color: #fff;
            width: 96px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }

        .btn {
            position: absolute;
            right: 10px;
            bottom: 10px;
            color: #fff;
            width: 28px;
            line-height: 20px;
            height: 20px;
            font-size: 14px;
            background-color: #FF4949;
            padding: 2px 12px 3px;
            border-radius: 12px;
        }
    }

}
