@charset "UTF-8";
.m-big-img {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 10px;
  border-radius: 5px;
  font-size: 0;
  /*.post {*/
  /*    width: 100%;*/
  /*    height: 208px;*/
  /*}*/ }
  .m-big-img:hover {
    background-color: #f4f4f4; }
  .m-big-img img {
    width: 100%;
    height: 208px;
    border-radius: 5px;
    margin-bottom: 10px; }
  .m-big-img .title, .m-big-img .des {
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis; }
  .m-big-img .title {
    height: 22px;
    font-weight: 500;
    font-size: 16px;
    color: #222222;
    margin-bottom: 8px; }
  .m-big-img .des {
    height: 18px;
    color: #999999;
    font-size: 12px;
    margin-bottom: 7px; }
  .m-big-img .foot-info {
    height: 27px; }
    .m-big-img .foot-info .price {
      font-size: 22px;
      color: #BE9E4D;
      margin-right: 6px; }
      .m-big-img .foot-info .price:before {
        font-size: 14px;
        content: "Â¥"; }
    .m-big-img .foot-info .source-price {
      color: #999;
      text-decoration: line-through;
      font-size: 12px; }
    .m-big-img .foot-info .per-consumer {
      color: #BE9E4D;
      font-size: 12px; }
    .m-big-img .foot-info .location {
      margin-top: 10px;
      float: right;
      color: #999;
      font-size: 12px; }
