.cardContainer {
  width: 100%;
  border-left: 1px solid #e5e5e5;
  border-right: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.cardContainer dl {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 44px;
}
.cardContainer dl dt, .cardContainer dl dd {
      color: #ffffff;
      height: 44px;
      line-height: 44px;
      padding: 0 5px;
}
.cardContainer dl dt {
      font-size: 18px;
      margin: 0 10px 0 13px;
      font-family: MFShangHei-Regular;
}
.cardContainer dl dd {
      font-size: 14px;
      font-weight: 400;
      cursor: pointer;
      position: relative;
}
.cardContainer dl dd.active::after {
        position: absolute;
        left: 50%;
        bottom: 0;
        content: " ";
        width: 2px;
        height: 0;
        margin-left: -6px;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        border-bottom: 7px solid #ffffff;
}
.cardContainer .cards {
    padding: 11px 10px 10px 10px;
    background-color: #ffffff;
}
.cardContainer .cards .el-col {
      height: 314px;
}


